const envApiUrl = import.meta.env.VITE_API_URL;
console.log('[API Config] Environment API URL:', envApiUrl);

export const API_BASE_URL = import.meta.env.PROD
  ? 'https://machine-shop-website.onrender.com/api'
  : 'http://localhost:3001/api';
console.log('[API Config] Final API Base URL:', API_BASE_URL);

export const API_ROUTES = {
  LOGIN: `${API_BASE_URL}/auth/login`,
  INVENTORY: `${API_BASE_URL}/inventory`,
  BARRELS: `${API_BASE_URL}/barrels`,
  ANNOUNCEMENTS: `${API_BASE_URL}/announcements`,
};

console.log('[API Config] API Routes:', API_ROUTES);

export const getAuthHeaders = () => {
  const token = localStorage.getItem('token');
  const headers = {
    'Content-Type': 'application/json',
    Authorization: token ? `Bearer ${token}` : '',
  };
  console.log('[API Config] Request Headers:', headers);
  return headers;
}; 